export default {
  code: '',
  component: '/components/gl-form-combination/Index.vue',
  opts: {
    ui: {
      cards: [
        {
          type: 'form',
          opts: {
            type: 'object',
            // 
            defaultEntity: 'platform_module',
            // update|create|read
            state: 'save',
            properties: {
              // id:{}id
              id: {},
              title: {
                control: 'input',
                title: '',
                // 
                disabled: false,
                // 
                readonly: false,
                // hiddenlayouthidden
                rules: [
                  {
                    type: 'empty',
                    prompt: ''
                  }
                ]
              },
              code: {
                control: 'input',
                title: '',
                rules: [
                  {
                    type: 'empty',
                    prompt: ''
                  }
                ],
                placeholder: ''
              },
              index: {
                control: 'input',
                title: '',
                placeholder: '/#/m/platform-ide/designer/Index',
                rules: [
                  {
                    type: 'empty',
                    prompt: ''
                  }
                ]
              },
              html: {
                control: 'input',
                title: 'html',
                placeholder: '<bold></bold>',
              },
              resize: {
                control: 'dropdown',
                title: '',
                tips: '',
                // dsdsdata
                data: [
                  {text: '', value: 'min'},
                  {text: '', value: 'max'}
                ],
                value: 'min'
              },
              seq: {
                control: 'input',
                title: '',
                readonly: true,
                value: -1
              },
              enabled: {
                control: 'checkbox',
                title: '',
                value: true
              },
              description: {
                control: 'textarea',
                title: '',
              },
            },
            layout: {
              type: 'table',
              data: [
                // [label colSpan,rowSpan,field colSpan,rowSpan]
                [{title: [4, 8]}, {code: [4, 8]}],
                [{index: [4, 20]}],
                [{resize: [4, 8]}, {enabled: [4, 8]}],
                [{html: [4, 8]}, {seq: [4, 8]}],
                [{description: [4, 20]}]
              ],
              hidden: {
                // 
                update: {},
                create: {},
                read: {}
              }
            },
            ds: {},
            vars: {}
          }
        },
        {
          name: "",
          type: "toolbar",
          opts: {
            appendToParent: true,
            actions: [
              {
                color: "primary",
                title: "",
                click: "@:$ctx.$_save()"
              },
              {
                color: "negative",
                title: "",
                click: "@:$ctx.$parent.$_cancel()"
              }
            ]
          }
        }
      ],
    }
  },
  query: {id: '@.id'}
}
