<template>
    <div class="gl-page-content-wrapper">
        <gl-layout-page title="" right-title="">
            <div slot="left">
                <gl-group item=".list>.item">
                    <div class="ui middle aligned divided selection animated list">
                        <div class="item" v-for="item in entityNames" @click="$_loadMeta(item)">
                            <div class="description" :class="{header:currentEntityName===item}">
                                {{item}}
                            </div>
                        </div>
                    </div>
                </gl-group>
            </div>
            <div slot="leftAction">
                <div class="item">
                    <!--<a class="ui mini button" :class="$gl.ui.color.primary" @click="newPlan.state='editing'"></a>-->
                </div>
            </div>
            <div slot="rightAction">
                <div class="item">
                    <!--<router-link-->
                    <!--:to="{ path: '/m/project-base/schedule/'+projectConfig.page.planConfig, query: { id: currentPlan.id,name: currentPlan.name }}"-->
                    <!--class="ui mini button" :class="$gl.ui.color.primary">-->
                    <!--</router-link>-->
                </div>
            </div>
            <div slot="right">
                <table class="ui fluid compact celled table">
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>precision</th>
                        <th>scale</th>
                    </tr>
                    <tr v-for="(item,index) in meta">
                        <td>{{index+1}}</td>
                        <td>{{item.name}}</td>
                        <td>{{item.title}}</td>
                        <td>{{item.type}}</td>
                        <td>{{item.nullable}}</td>
                        <td>{{item.charMaxLength}}</td>
                        <td>{{item.precision}}</td>
                        <td>{{item.scale}}</td>
                    </tr>
                </table>
            </div>
        </gl-layout-page>
    </div>
</template>
<script>

    export default {
        props: {
            defaultEntity: {
                type: String
            }
        },
        data() {
            return {
                // 
                currentEntityName: '',
                // 
                entityNames: [],
                // 
                meta: []
            }
        },
        computed: {},
        created() {
            this.$_loadEntityNames()
            this.$_loadMeta(this.defaultEntity)
        },
        watch: {
            'defaultEntity': function (val, oval) {
                this.$_loadMeta(this.defaultEntity)
            }
        },
        mounted() {
        },
        updated() {
        },
        methods: {
            $_loadEntityNames() {
                let thisVue = this
                thisVue.$gl.data.queryEntityNames().then(function (res) {
                    console.log(res)
                    thisVue.entityNames = res.data.sort()
                })
            },
            $_loadMeta(entityName) {
                if (!entityName) {
                    return
                }
                let thisVue = this
                thisVue.currentEntityName = entityName
                thisVue.$gl.data.queryMeta(thisVue.currentEntityName).then(function (res) {
                    thisVue.meta = res.meta
                    thisVue.$emit('select', {name: entityName, meta: thisVue.meta})
                })
            }
        },
        components: {}
    }
</script>
