export default [
  {
    text: '', value: 0, data: {
      type: 'object',
      // 
      defaultEntity: 'platformUser',
      // update|create|read
      state: 'save',
      properties: {
        // id:{}id
        id: {},
        name: {
          control: 'input',
          title: '',
          // 
          disabled: true,
          // 
          readonly: false
          // hiddenlayouthidden
        },
        loginName: {
          control: 'input',
          title: '',
          rules: [
            {
              type: 'empty',
              prompt: ''
            }
          ]
        },
        password: {
          control: 'password',
          title: '',
          tips: '6',
          rules: [
            {
              type: 'empty',
              prompt: ''
            },
            {
              type: 'minLength[6]',
              prompt: '6'
            }
          ]
        },
        confirmPassword: {
          control: 'password',
          title: '',
          rules: [
            {
              type: 'match[password]',
              prompt: ''
            }
          ]
        },
        email: {
          control: 'email',
          title: '',
          placeholder: 'xxx@xxx.xxx',
        },
        age: {
          control: 'input',
          title: '',
          // 
          value: '20',
        },
        avatar: {
          control: 'image',
          title: '',
          value: 'elliot',
        },
        sex: {
          control: 'dropdown',
          title: '',
          // dsdsdata
          data: [
            {text: '', value: ''},
            {text: '', value: 'male'},
            {text: '', value: 'female'}
          ],
          value: ''
        },
        tel: {
          control: 'input',
          title: '',
          // teltelfieldfieldfield:'tel'
          field: 'telephone',
          // 
          entity: 'platformUser',
          placeholder: '',
          rules: [
            {
              type: 'empty',
              prompt: ''
            }
          ]
        },
        province: {
          control: 'dropdown',
          title: '',
          ds: 'province',
          // 
          value: '440000'
        },
        city: {
          control: 'dropdown',
          title: '',
          // cityDS
          ds: 'city'
        },
        enable: {
          control: 'checkbox',
          title: '',
          value: true
        },
        description: {
          control: 'textarea',
          title: ''
        },
        code: {
          control: 'input',
          title: 'DEMO',
          entity: 'platformDemoEntity',
          description: ''
        },
        type: {
          control: 'radio',
          title: '',
          value: 'typeB',
          data: [
            {text: 'A', value: 'typeA'},
            {text: 'B', value: 'typeB'},
            {text: 'C', value: 'typeC'}
          ]
        },
        typeA: {
          control: 'input',
          title: 'A'
        },
        typeB: {
          control: 'input',
          title: 'B',
          readonly: true,
          js: "gs:$ctx.form.typeB=$ctx.form.province+'-'+$ctx.form.city"
        },
        typeC: {
          control: 'input',
          title: 'C'
        },
        demoName: {
          control: 'input',
          title: 'DEMO',
          // namedemoName
          field: 'name',
          entity: 'platformDemoEntity',
          description: ''
        },
        demoDescription: {
          control: 'textarea',
          title: 'DEMO',
          // $ctx.id
          isComputeInServer: true,
          value: '$ctx.id',
          // namedemoName
          field: 'description',
          entity: 'platformDemoEntity',
          description: ''
        },
        // userId: {
        //     control: 'input',
        //     title: 'id',
        //     isComputeInServer: true,
        //     value: '$ctx.id',
        //     entity: 'platformRole',
        //     description: 'userIdid'
        // }
        // ,
        roleDescription: {
          control: 'input',
          title: '',
          isComputeInServer: true,
          value: '$ctx.demoDescription',
          field: 'description',
          entity: 'platformRole',
          description: 'descriptionplatformDemoEntitydemoDescription'
        }
        ,
        AppDescription: {
          control: 'input',
          title: '',
          isComputeInServer: true,
          value: '$ctx.roleDescription',
          field: 'description',
          entity: 'platformApp',
          description: 'appdescriptionplatformRoleroleDescription'
        }
      },
      layout: {
        type: 'table',
        data: [
          // [label colSpan,rowSpan,field colSpan,rowSpan]
          [{name: [4, 8]}, {avatar: [4, 8, 4]}],
          [{loginName: [4, 8]}],
          [{email: [4, 8]}],
          [{age: [4, 8]}],
          [{sex: [4, 8]}, {tel: [4, 8]}],
          [{province: [4, 8]}, {city: [4, 8]}],
          // $title$styletd
          [{$title: '<div></div>', $style: 'background-color:#FFFFFF'}],
          [{password: [4, 8]}, {confirmPassword: [4, 8]}],
          [{enable: [4, 8]}, {'': [4, 8]}],
          [{code: [4, 8]}, {demoName: [4, 8]}],
          [{type: [4, 20]}],
          [{typeA: [4, 20]}],
          [{typeB: [4, 20]}],
          [{typeC: [4, 20]}],
          [{description: [4, 20]}]
        ],
        hidden: {
          // 
          common: {
            typeA: 'gs:$ctx.form.type!=="typeA"',
            typeB: 'gs:$ctx.form.type!=="typeB"',
            typeC: 'gs:$ctx.form.type!=="typeC"'
          },
          update: {password: 1, confirmPassword: 2},
          create: {},
          read: {}
        }
      },
      ds: {
        province: {
          entity: 'platform_province',
          // default false
          lazy: false,
          // 
          fields: 'name text,code value',
          description: ''
        },
        city: {
          entity: 'platform_city',
          lazy: true,
          fields: 'name text,code value',
          // 
          params: {
            // province
            provinceCode: 'gs:$ctx.form.province'
          },
          description: ''
        }
      },
      vars: {
        myVarA: {
          description: 'myVarA30',
          value: '30'
        }
      }
    }
  }
]
