/**
 *  
 */
class SimplePageDefinition {
  /**
   * @param options
   * @param isFromRemote false
   */
  constructor (options, isFromServer = false) {
    this.reset(options)
    this.isFromServer = isFromServer
  }

  reset (options) {
    let params = options || {}
    let defaultContent = {
      code: {
        mvel: '',
        sql: ''
      },
      // 
      // 1'/components/collections/grid'
      // 2vue component{template:''}
      component: params.component || '',
      // componentopts
      opts: {
        // 
        // body: {},
        // 
        // elements:{'id1':{'ui':{}},'id2':{}}
        // elements: {},
        // 
        // tree:['id1':['id3','id4'],'id2']
        // tree: {}
      },
      // optsquery
      params: {}
    }
    // id(guid)
    this.id = params.id || ''
    // id
    this.extendId = params.extendId
    //  
    // this.name = params.name || ''
    // apiformtable
    this.type = params.type || ''
    // 
    this.code = params.code || ''
    // 
    this.description = params.description || ''
    // componentoptsparams
    this.content = params.content ? this.parseContent(params.content) : defaultContent
  }

  /**
   * @param content 
   * @returns {*} content 
   */
  parseContent (content) {
    if (typeof content === 'string') {
      let c = JSON.parse(content)
      return {
        component: c.component,
        opts: c.opts,
        params: c.params
        // code: c.code
      }
    } else {
      return content
    }
  }

  // codecomponentoptsparamspageCfg
  // toPageCfg () {
  //   return {
  //     code: this.code,
  //     component: this.content.component,
  //     opts: this.content.opts,
  //     params: this.content.params
  //   }
  // }
}

export default SimplePageDefinition
